import { Component, Input, OnChanges } from "@angular/core";

@Component({
  selector: 'detail-panel',
  template: require('./detailPanel.component.html'),
})
export class DetailPanelComponent {
  
  @Input() title: string;
  @Input() collapsed: boolean;

  collapse() {
    this.collapsed = !this.collapsed;
  }
}





